	module.exports = {"data":{"post":{"html":"<p><a href=\"http://fami-siegel.de/\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2010/04/FaMi-Siegel.png\" alt=\"FaMi-Siegel.png\" border=\"0\" width=\"180\" height=\"180\" align=\"right\" /></a>Last week the organizers of the <a href=\"http://fami-siegel.de/\">FaMi-Siegel</a> held a press conference to present the <a href=\"http://www.papascott.de/archives/2010/04/11/certified-family-friendly-employer/\">familiy-friendly employers</a> they honored. We've been making extensive use of the seal in printed and email correspondence, and had stickers printed for the restaurant in Dibbersen and all our company cars.</p>\n<p>The stickers are only 15 cm in diameter, and Frauke wanted something that would stand out for the press photo. We thought about an A4 laser printout, but then asked our printer for advice. Usually he find less expensive alternatives for us, but this time he suggested an upgrade. \"A4 is way too small! You want 50 cm diameter! On a board!\"</p>\n<p>The Hamburger Abendblatt was impressed.</p>\n<p><a href=\"http://www.abendblatt.de/region/harburg/article1495435/Familienfreundliche-Firmen-erwuenscht.html\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2010/05/2010-05-22-10.18.15.jpg\" alt=\"2010-05-22 10.18.15.jpg\" border=\"0\" width=\"480\" height=\"360\" /></a></p>\n<p>That's Frauke in the front row with her big seal on page 2 of the Harburg section. The <a href=\"http://www.abendblatt.de/region/harburg/article1495435/Familienfreundliche-Firmen-erwuenscht.html\">photo is online at abendblatt.de</a>. Everyone can see the photo, but only subscribers can read the article.</p>","fields":{"title":"Big Seal","date":"22 May 2010","url":"https://papascott.de/2010-05-22-big-seal/"}}},"pathContext":{"slug":"/2010-05-22-big-seal/","prev":{"fields":{"title":"Back to the Roots","date":"14 May 2010","slug":"/2010-05-14-back-to-the-roots/"}},"next":{"fields":{"title":"The Backyard","date":"02 Jun 2010","slug":"/2010-06-02-the-backyard/"}}}}

